@import-normalize;
/* bring in normalize.css styles */

:root {
    --background-color: #0c0d0d;
    --box-background-color: #171719;
    --button-color: #0ef387;
    --hover-color: #0ebb69;
    --text-color: #02b15a;
    --success-color: #097b45;
    --errors-color: #e74a3b;
    --main-text-color: #fafafa;
    --regular-text-color: rgba(250, 250, 250, 0.4);
    --text-color_50: rgba(250, 250, 250, 0.5);
    --button-color-modal: #29292b;
    --regular-text-color01: rgba(250, 250, 250, 0.1);
    --black06: rgba(12, 13, 13, 0.6);
    --black05: rgba(12, 13, 13, 0.5);
    --regular-text-color-66: #fafafa66;
    --text-black-color: #11101c;
    --text-dark-blue-color: #11101c80;
    --bg-container-percent: #02b15a26;
    --border-color: #fafafa33;
    --text-link-color: #fafafa99;
    --border-field-color: rgba(250, 250, 250, 0.2);
    --list-item-hover: rgba(0, 0, 0, 0.2);

    /* Font-size */
    --font-size-mini: 12px;
    --font-size-small: 14px;
    --font-size-smaller: 16px;
    --font-size-mediumSm: 18px;
    --font-size-medium: 20px;
    --font-size-large: 24px;
    --font-size-modal-title: 28px;
    --font-size-title-mob: 32px;
    --font-size-title: 38px;
    --font-size-main-title: 56px;
    --transition-delay: 350ms;
    --transition: ease;
}

*,
::after,
::before {
    box-sizing: border-box;
}

@font-face {
    font-family: 'SuisseIntl-Regular';
    src: url('./assets/fonts/SuisseIntl-Regular.woff2') format('woff2'),
        url('./assets/fonts/SuisseIntl-Regular.woff') format('woff');
    font-weight: 400;
    font-stretch: normal;
    font-display: swap;
}

@font-face {
    font-family: 'SuisseIntl-Bold';
    src: url('./assets/fonts/SuisseIntl-Bold.woff2') format('woff2'),
        url('./assets/fonts/SuisseIntl-Bold.woff') format('woff');
    font-weight: 700;
    font-stretch: normal;
    font-display: swap;
}

body,
html {
    margin: 0;
    box-sizing: border-box;
    background-color: var(--background-color);
    /* #0c0d0d */
    font-family: 'SuisseIntl-Regular', sans-serif;
    font-weight: 400;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
    margin: 0;
}

ul,
ol {
    list-style: none;
    margin: 0;
    padding: 0;
}

img {
    display: block;
    max-width: 100%;
    /* height: auto; */
}

button {
    padding: 0;
    border: none;
    font: inherit;
    color: inherit;
    background-color: var(--button-color);
    cursor: pointer;
}

a {
    text-decoration: none;
    color: inherit;
}

img {
    display: block;
    max-width: 100%;
    height: auto;
}

/* Scrollbar */
.scroll::-webkit-scrollbar-thumb {
    background-color: #444446;
    border-radius: 12px;
}

/* For TransactionChart */
.scrollA::-webkit-scrollbar {
    width: 6px;
}

.scrollA::-webkit-scrollbar-track {
    background-color: var(--background-color);
}

/* For Modal EditCategories, HistoryTransactions */
.scrollB::-webkit-scrollbar {
    width: 8px;
    height: 8px;
}

.scrollB::-webkit-scrollbar-track {
    background-color: transparent;
}

.scrollС::-webkit-scrollbar {
    height: 8px;
    width: 8px;
}

.scrollC::-webkit-scrollbar-track {
    background-color: transparent;
}

.container {
    margin: 0 auto;
    padding: 0 20px;

    min-width: 320px;
    max-width: 375px;
}

@media only screen and (min-width: 768px) {
    .container {
        max-width: 768px;
        padding: 0 32px;
    }
}

@media only screen and (min-width: 1440px) {
    .container {
        max-width: 1440px;
        padding: 0 100px;
    }
}