.userPanelBody {
    position: absolute;
    padding: 16px;
    border: 1px solid rgba(250, 250, 250, 0.1);
    border-radius: 15px;
    width: 194px;
    background: #0c0d0d;
    display: flex;
    flex-direction: column;
    gap: 16px;
    justify-content: center;
    top: 50px;

    font-family: 'SuisseIntl-Regular', sans-serif;
    font-weight: 400;
    font-size: 14px;
    color: rgba(250, 250, 250, 0.5);
    z-index: 999;
}

.userPanelItemsWrapper {
    display: flex;
    align-items: center;
    gap: 8px;
    background-color: transparent;
}

.userPanelItemsWrapper:hover .userIcon {
    stroke: #0ef387;
}

.userPanelItemsWrapper:hover .logOutIcon {
    stroke: #0ef387;
}

.userPanelItemsWrapper:hover .userPanelLinkText {
    color: #fafafa;
}

.userIcon {
    fill: none;
    width: 20px;
    height: 20px;
    stroke: #838383;
    stroke-width: 1.3;
    stroke-linecap: round;
    stroke-linejoin: round;
    transition: var(--transition) var(--transition-delay);
}

.logOutIcon {
    fill: none;
    width: 20px;
    height: 20px;
    stroke: #838383;
    stroke-width: 1.3;
    stroke-linecap: round;
    stroke-linejoin: round;
    transition: var(--transition) var(--transition-delay);
}

.userPanelLinkText {
    font-family: 'SuisseIntl-Regular', sans-serif;
    font-weight: 400;
    font-size: 14px;
    color: rgba(250, 250, 250, 0.5);
    transition: var(--transition) var(--transition-delay);
}

@media screen and (min-width: 1440px) {
    .userPanelBody {
        width: 217px;
        top: 65px;
    }
}

.userPanelBtn {
    display: flex;
    align-items: center;
    gap: 8px;
    background-color: transparent;
}

.userPanelBtn:hover .userIcon {
    stroke: #0ef387;
}

.userPanelBtn:hover .logOutIcon {
    stroke: #0ef387;
}

.userPanelBtn:hover .userPanelLinkText {
    color: #fafafa;
}

 