.buttonWrapper {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    display: flex;
    align-items: center;
    flex-direction: column;
    gap: 16px;
}

.mobileNavBtn {
    border-radius: 30px;
    padding: 12px 21px;
    width: 115px;
    height: 40px;
    font-family: 'SuisseIntl-Regular', sans-serif;
    font-weight: 400;
    font-size: 13px;
    letter-spacing: -0.02em;
    border: 1px solid rgba(12, 13, 13, 0.4);
    transition: var(--transition) var(--transition-delay);
}

.navBtnActive {
    width: 115px;
    height: 40px;
    background-color: black;
    border: 1px solid var(--button-color);
    font-size: 13px;
    color: #fafafa;
    border-radius: 30px;
}

@media screen and (min-width: 768px) {
    .mobileNavBtn {
        width: 125px;
        height: 43px;
        font-size: 15px;
    }

    .navBtnActive {
        width: 125px;
        height: 43px;
        font-size: 15px;
    }
}

@media screen and (min-width: 1440px) {
    .buttonWrapper {
        flex-direction: row;
    }

    .navBtnActive {
        background-color: var(--button-color);
        border: 1px solid var(--button-color);
        font-size: 15px;
        color: #fafafa;
        border-radius: 30px;
        color: var(--background-color);
    }

    .mobileNavBtn {
        background-color: transparent;
        border: 1px solid rgba(250, 250, 250, 0.4);
        font-size: 15px;
        color: #fafafa;
    }

    .mobileNavBtn:hover {
        border: 1px solid var(--button-color);
    }
}